
@font-face {
  font-family: tawbah100;
  src: url(./assets/fonts/tawbah100.ttf);
}

@import url('https://fonts.googleapis.com/css2?family=Raleway:ital,wght@0,300;0,400;0,500;0,600;1,300&display=swap');
* {
  box-sizing: border-box;
}
p {
  color: #fff;
}

a {
  color: #fff;
  text-decoration: none;
  width: auto;
  height: auto;
}

h2 {
  color: #fff;
}

body {
  font-family: 'Raleway', sans-serif;
  background-color: #171717;
  margin: 0;
  padding: 0;
  padding-top: 60px;
}

header {
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  background-color: #262626;
  border-bottom: 1px solid #313131;
  padding: 20px;
}

nav {
  display: flex;
  justify-content: space-between;
  align-items: center;
}

nav a {
  text-decoration: none;
  font-size: 16px;
  font-weight: bolder;
}

nav a:hover {
  text-decoration: underline;
}

/* Slider */
.slider {
  background-color: #171717;
  /* border-bottom:0.2px solid #d0d0d0; */
  margin: 0;
}

.container {
  margin: 0 auto; /* centers the posts horizontally */
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
  align-items: center;
  height: 40vh;
  max-width: 1000px;
  width: 95%;
}

.text-wrapper {
  text-align: center;
  padding: 10px;
}

.centered-text {
  text-align: center;
  margin: 0;
  font-family: tawbah100;
  font-size: x-large;
}

.centered-text-2 {
  text-align: center;
  margin-top: 30px;
  font-size: medium;
  font-weight:bold;

}

/*  Sabiquns */
.sabiqun-posts {
  background-color: #1E1E1E;
}


.posts {
  max-width: 1300px;
  margin: 0 auto; /* centers the posts horizontally */
  display: flex;
  flex-wrap: wrap;
  justify-content: space-between;
  padding:  10px;
  padding-top: 20px;
}

.post {
  margin-top: 30px;
  background-color: #242424;
  border-radius: 20px;
  width: 100%;
  margin-bottom: 20px;
  padding: 20px;
  border: 0.1px solid #3a3a3a;
}
.post:hover {
  background-color: #292929;
}

.post-header {
  display: flex;
  align-items: center;
  justify-content: space-between;
}

.post-title {
  font-size: 24px;
  margin: 0;
  margin-bottom: 10px;
}

.post-image {
  width: 30px;
  height: 30px;
  object-fit: cover;
  border-radius: 50%;
}


.post-author {
  font-size: 16px;
  margin: 0;
  margin-bottom: 10px;
}

.post-summary {
  font-size: 18px;
  margin: 0;
  margin-bottom: 20px;
}

.post-readmore {
  display: block;
  text-align: right;
  font-size: 16px;

  text-decoration: none;
}

.post-readmore:hover {
  text-decoration: underline;
}


@media (min-width: 768px) {
  .post {
    width: calc(50% - 10px);
  }
}
@media (max-width: 768px) {
  .centered-text {
    font-size: large;
  }
  .centered-text-2 {
    font-size: small;
  }
}